//=require components/menu.js

$(document).ready(function () {
    
    // Slider

    function windowSize() {
        if ($(window).width() <= '481') {
            $('.card-box').addClass('slick-slide2');
            $('.process-box').addClass('slick-slide3');
        } else {
            $('.card-box').removeClass('slick-slide2');
            $('.process-box').removeClass('slick-slide3');
        }
    }
    $(window).on('load resize', windowSize);
    
    $('#slider').slick({
        prevArrow: '<i class="prev-arrow slick-arrow"></i> ',
        nextArrow: '<i class="next-arrow slick-arrow"></i> ',
        autoplay: true
    });

    $('#card-box').slick({
        prevArrow: '<i class="prev-arrow slick-arrow"></i> ',
        nextArrow: '<i class="next-arrow slick-arrow"></i> ',
        responsive: [
            {
                breakpoint: 2000 ,
                settings: "unslick"
            },
            {
            breakpoint: 482,
            settings: "slick"
        }
        ]
    });
    
    $('#process-box').slick({
        prevArrow: '<i class="prev-arrow slick-arrow"></i> ',
        nextArrow: '<i class="next-arrow slick-arrow"></i> ',
        infinite: false,
        responsive: [
            {
                breakpoint: 2000,
                settings: "unslick"
            },
            {
            breakpoint: 482,
            settings: "slick"
        }
        ]
    });

    // Cards

    $('.card-box .card').mouseenter(function () {
        $(this).find('.card-text').show();
    })
    $('.card-box .card').mouseleave(function () {
        $(this).find('.card-text').hide();
    })

    // Process

    $('.process .step').mouseenter(function () {
        $(this).find('.second-view').show();
    })
    $('.process .step').mouseleave(function () {
        if ($(window).width() > 481) {
            $(this).find('.second-view').hide();
            
        }
        
    })

    // Spoiler

    $('.spoiler').click(function () {
        $(this).prev().slideToggle();
        $(this).toggleClass('close');
        if ($(this).text() === 'Развернуть') {
            $(this).text('Свернуть');
        } else {
            $(this).text('Развернуть');
        }
    })

});

